@import "../../../styles.scss";

div .linkButton__card {
    background: #fafafa;
    margin: 12px;
    padding: 8px;

    height: 50px;

    position: relative;
    display: flex;
    text-align: center;
    align-items: center;
    align-content: space-around;

    border-radius: 10px;

    box-shadow: 2px 2px rgba(0, 0, 0, .2);

    /*
    &:hover {
        background: #d3d3d3;
    }
    */

    &:active {
        background: #ededed;
    }

    .linkButton__icon {

        width: 25%;

        img {
            width: 25px;
            height: 25px;
            top: 30px;
        }
    }

    .linkButton__textbox {

        width: 50%;
        text-align: left;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: space-between;

        border-left : 1px solid rgba(0, 0, 0, .2);
        padding-left : 15px;
        height : 32px;

        .linkButton__comment {

            font-size: 1rem;

        }
        
        .linkButton__link {

            width: 50vw;

            color: #999999;
            font-size: 0.75rem;
            margin-top: 5px;
            //font-family: "Nanum plus condesed";
            
        }
    }


    .linkButton__arrow {

        width: 25%;
        text-align: right;
        padding : 12px;
        font-size: 0.6rem;
    }

    .overflow_ellipsis {
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
    }

}

@media ( min-width: 768px ) {
    div .linkButton__card {
        width: 768px;

        .linkButton__textbox{
            .linkButton__link {
                font-family: "Nanum plus";
                
            }
        }
    }
}